/**
 * This interface describes the mock-debug specific launch attributes
 * (which are not part of the Debug Adapter Protocol).
 * The schema for these attributes lives in the package.json of the mock-debug extension.
 * The interface should always match this schema.
 */
/*
	このインタフェースは、モックデバッグ特有の起動属性を記述します。
	(Debug Adapter Protocol の一部ではありません)。
	これらの属性のスキーマは、mock-debug 拡張モジュールの package.json にあります。
	インターフェイスは常にこのスキーマと一致していなければなりません。
*/
interface LaunchRequestArguments extends DebugProtocol.LaunchRequestArguments {
	program			: string;	// デバッグする「プログラム」への絶対パス
	stopOnEntry?	: boolean;
	trace?			: boolean;	// デバッグアダプタプロトコルのロギングを有効にする
}

function timeout(ms: number) {return new Promise(re=> setTimeout(re, ms));}

class DebugAdapter extends LoggingDebugSession {
	// 複数のスレッドをサポートしないので、デフォルトのスレッドにハードコードされたID
	private static	readonly THREAD_ID = 1;

	private	readonly	dbg	= new Debugger();	// runtime (or debugger)

	private	_reportProgress		= false;
	private	_progressId			= 10000;
	private	_cancelledProgressId	: string | undefined = undefined;

	// 1 つのデバッグ・セッションで使用する新しいデバッグ・アダプタ
	// デバッグ・アダプタのデフォルトの実装を設定
	// セッションごと【▶（デバッグの開始）や⟲（再起動ボタン）】にここから再開する
	constructor() {
		super('mock-debug.txt');
//console.log(`fn:mockDebug.ts line:136 DebugSS constructor`);
		this.setDebuggerLinesStartAt1(false);	// zero-based lines and col.
		this.setDebuggerColumnsStartAt1(false);

		// setup event handlers
		this.dbg.on('stopOnEntry', ()=> {
//console.log(`fn:mockDebug.ts line:71 stopOnEntry`);
			this.sendEvent(new StoppedEvent('entry', DebugAdapter.THREAD_ID));
		});
		this.dbg.on('stopOnStep', ()=> {		// F10
//console.log(`fn:mockDebug.ts line:75 stopOnStep`);
			this.sendEvent(new StoppedEvent('step', DebugAdapter.THREAD_ID));
		});
		this.dbg.on('stopOnBreakpoint', ()=> {
//console.log(`fn:mockDebug.ts line:79 stopOnBreakpoint`);
			this.sendEvent(new StoppedEvent('breakpoint', DebugAdapter.THREAD_ID));
		});
		this.dbg.on('stopOnDataBreakpoint', ()=> {
//console.log(`fn:mockDebug.ts line:83 stopOnDataBreakpoint`);
			this.sendEvent(new StoppedEvent('data breakpoint', DebugAdapter.THREAD_ID));
		});
		this.dbg.on('stopOnException', ()=> {
//console.log(`fn:mockDebug.ts line:87 stopOnException`);
			this.sendEvent(new StoppedEvent('exception', DebugAdapter.THREAD_ID));
		});
		this.dbg.on('breakpointValidated', (bp: MockBreakpoint)=> {
//console.log(`fn:mockDebug.ts line:91 breakpointValidated`);
			this.sendEvent(new BreakpointEvent('changed', <DebugProtocol.Breakpoint>{ verified: bp.verified, id: bp.id }));
		});
		this.dbg.on('output', (text, filePath, line, column)=> {
//console.log(`fn:mockDebug.ts line:95 output`);
			const e: DebugProtocol.OutputEvent = new OutputEvent(`${text}\n`);

			if (text === 'start' || text === 'startCollapsed' || text === 'end') {
				e.body.group = text;
				e.body.output = `group-${text}\n`;
			}

			e.body.source = this.createSource(filePath);
			e.body.line = this.convertDebuggerLineToClient(line);
			e.body.column = this.convertDebuggerColumnToClient(column);
			this.sendEvent(e);
		});
		this.dbg.on('end', ()=> this.sendEvent(new TerminatedEvent()));
	}

	// initialize ... デバッグアダプタが提供する機能を調べるためにフロントエンドから呼び出される最初のリクエスト
	protected initializeRequest(res: DebugProtocol.InitializeResponse, args: DebugProtocol.InitializeRequestArguments): void {
		// https://microsoft.github.io/debug-adapter-protocol/overview
		// Specification https://microsoft.github.io/debug-adapter-protocol/specification#Types_InitializeRequestArguments
//console.log(`"res":${JSON.stringify(res)}, "args":${JSON.stringify(args)}`);
/*{
	"res": {
		"seq": 0,
		"type": "response",
		"request_seq": 1,
		"command": "initialize",
		"success": true,
		"body": {}
	},
	"args": {
		"clientID": "vscode",
		"clientName": "Visual Studio Code",
		"adapterID": "skynovel",
		"pathFormat": "path",
		"linesStartAt1": true,
		"columnsStartAt1": true,
		"supportsVariableType": true,
		"supportsVariablePaging": true,
		"supportsRunInTerminalRequest": true,
		"locale": "ja",
		"supportsProgressReporting": true
	}
}*/
		if (args.supportsProgressReporting) this._reportProgress = true;

		// build and return the capabilities of this debug adapter:
		res.body = res.body ?? {};

		// the adapter implements the configurationDoneRequest.
		res.body.supportsConfigurationDoneRequest = true;

		// make VS Code to use 'evaluate' when hovering over source
		res.body.supportsEvaluateForHovers = true;

		// make VS Code to show a 'step back' button
		res.body.supportsStepBack = true;

		// make VS Code to support data breakpoints
		res.body.supportsDataBreakpoints = true;

		// make VS Code to support completion in REPL
		res.body.supportsCompletionsRequest = true;
		res.body.completionTriggerCharacters = ['.', '['];
			// TODO: completionTriggerCharacters

		// make VS Code to send cancelRequests
		res.body.supportsCancelRequest = true;

		// make VS Code send the breakpointLocations request
		res.body.supportsBreakpointLocationsRequest = true;

		this.sendResponse(res);

		// since this debug adapter can accept configuration requests like 'setBreakpoint' at any time,
		// we request them early by sending an 'initializeRequest' to the frontend.
		// The frontend will end the configuration sequence by calling 'configurationDone' request.
		/*	なぜなら、このデバッグアダプタは 'setBreakpoint' のような設定要求をいつでも受け付けることができるからです。
			フロントエンドに 'initializeRequest' を送ることで早めにリクエストします。
			フロントエンドは 'configurationDone' リクエストを呼び出すことで設定シーケンスを終了します。
		*/
		this.sendEvent(new InitializedEvent());
	}
	// コンフィギュレーションシーケンスの最後にコールされます。
	// すべてのブレークポイントなどがDAに送信され、「起動」を開始できることを示します。
	protected configurationDoneRequest(res: DebugProtocol.ConfigurationDoneResponse, args: DebugProtocol.ConfigurationDoneArguments): void {
		super.configurationDoneRequest(res, args);
		this.cfgDone.notify();	// 設定が完了したことを launchRequest に通知
	}
	private	readonly	cfgDone = new Subject();	// 設定完了

	protected async launchRequest(res: DebugProtocol.LaunchResponse, args: LaunchRequestArguments) {
		// make sure to 'Stop' the buffered logging if 'trace' is not set
		// 'trace' が設定されていない場合は、バッファリングされたロギングを 'Stop'
		logger.setup(args.trace ?Logger.LogLevel.Verbose :Logger.LogLevel.Stop, false);

		// 設定が終わるまで待つ（そして configurationDoneRequest が呼び出される）
		await this.cfgDone.wait(1000);

		this.dbg.start(args.program, !!args.stopOnEntry);

		this.sendResponse(res);
	}

	protected setBreakPointsRequest(res: DebugProtocol.SetBreakpointsResponse, args: DebugProtocol.SetBreakpointsArguments): void {
// console.log(`"res":${JSON.stringify(res)}, "args":${JSON.stringify(args)}`);
/*{
	"res": {
		"seq": 0,
		"type": "response",
		"request_seq": 3,
		"command": "setBreakpoints",
		"success": true
	},
	"args": {
		"source": {
			"name": "main.sn",
			"path": "/Users/==/==/doc/prj/script/main.sn"	// 絶対パス
		},
		"lines": [
			19,		// ブレイクポイント行番号（表示と同じ）
			21
		],
		"breakpoints": [
			{
				"line": 19
			},
			{
				"line": 21
			}
		],
		"sourceModified": false
	}
}*/
		const clientLines = args.lines ?? [];

		const path = <string>args.source.path;
		this.dbg.clearBreakpoints(path);	// for this file

		// set and verify breakpoint locations
		const actualBreakpoints = clientLines.map(l => {
			const {verified, line, id} = this.dbg.setBreakPoint(path, this.convertClientLineToDebugger(l));
			const bp = <DebugProtocol.Breakpoint> new Breakpoint(verified, this.convertDebuggerLineToClient(line));
			bp.id = id;
			return bp;
		});
		// send back the actual breakpoint positions
		res.body = {breakpoints: actualBreakpoints};
		this.sendResponse(res);
	}

	protected breakpointLocationsRequest(res: DebugProtocol.BreakpointLocationsResponse, args: DebugProtocol.BreakpointLocationsArguments, _request?: DebugProtocol.Request): void {
		if (args.source.path) {
			const bps = this.dbg.getBreakpoints(args.source.path, this.convertClientLineToDebugger(args.line));
			res.body = {
				breakpoints: bps.map(col=> {return {
					line: args.line,
					column: this.convertDebuggerColumnToClient(col)
				}})
			};
		}
		else res.body = {breakpoints: []};
		this.sendResponse(res);
	}

	protected threadsRequest(res: DebugProtocol.ThreadsResponse): void {
		// ランタイムはスレッドをサポートしていないので、デフォルトのスレッドを返すだけ
		res.body = {
			threads: [new Thread(DebugAdapter.THREAD_ID, 'thread 1')]
		};
		this.sendResponse(res);
	}

	protected stackTraceRequest(res: DebugProtocol.StackTraceResponse, args: DebugProtocol.StackTraceArguments): void {
		const startFrame = typeof args.startFrame === 'number' ?args.startFrame :0;
		const maxLevels = typeof args.levels === 'number'? args.levels :1000;
		const endFrame = startFrame + maxLevels;
		const stk = this.dbg.stack(startFrame, endFrame);
		res.body = {
			stackFrames: stk.frames.map((f: any)=> new StackFrame(f.index, f.name, this.createSource(f.file), this.convertDebuggerLineToClient(f.line))),
			totalFrames: stk.count
		};
		this.sendResponse(res);
	}


	// 変数ビュー
	private	readonly	hdlsVar = new Handles<string>();
	protected scopesRequest(res: DebugProtocol.ScopesResponse, _args: DebugProtocol.ScopesArguments): void {
		res.body = {
			scopes: [
				new Scope('Local', this.hdlsVar.create('local'), false),
				new Scope('Global', this.hdlsVar.create('global'), true)
			]
		};
		this.sendResponse(res);
	}
	private	readonly	mapCancelationTokens = new Map<number, boolean>();
	private	readonly	mapIsLongrunning = new Map<number, boolean>();
	protected async variablesRequest(res: DebugProtocol.VariablesResponse, args: DebugProtocol.VariablesArguments, request?: DebugProtocol.Request) {
		const aVar: DebugProtocol.Variable[] = [];
		if (this.mapIsLongrunning.get(args.variablesReference)) {
			// long running

			if (request) this.mapCancelationTokens.set(request.seq, false);

			for (let i = 0; i < 100; i++) {
				await timeout(1000);
				aVar.push({
					name: `i_${i}`,
					type: 'integer',
					value: `${i}`,
					variablesReference: 0
				});
				if (request && this.mapCancelationTokens.get(request.seq)) {
					break;
				}
			}

			if (request) this.mapCancelationTokens.delete(request.seq);
		}
		else {
			const id = this.hdlsVar.get(args.variablesReference);
			if (id) {
				aVar.push({
					name: id + '_i',
					type: 'integer',
					value: '123',
					variablesReference: 0
				});
				aVar.push({
					name: id + '_f',
					type: 'float',
					value: '3.14',
					variablesReference: 0
				});
				aVar.push({
					name: id + '_s',
					type: 'string',
					value: 'hello world',
					variablesReference: 0
				});
				aVar.push({
					name: id + '_o',
					type: 'object',
					value: 'Object',
					variablesReference: this.hdlsVar.create(id + '_o')
				});

				// cancelation support for long running requests
				const nm = id + '_long_running';
				const ref = this.hdlsVar.create(id + '_lr');
				aVar.push({
					name: nm,
					type: 'object',
					value: 'Object',
					variablesReference: ref
				});
				this.mapIsLongrunning.set(ref, true);
			}
		}

		res.body = {variables: aVar};
		this.sendResponse(res);
	}

	protected continueRequest(res: DebugProtocol.ContinueResponse, _args: DebugProtocol.ContinueArguments): void {
		this.dbg.continue();
		this.sendResponse(res);
	}

	protected reverseContinueRequest(res: DebugProtocol.ReverseContinueResponse, _args: DebugProtocol.ReverseContinueArguments) : void {
		this.dbg.continue(true);
		this.sendResponse(res);
	}

	protected nextRequest(res: DebugProtocol.NextResponse, _args: DebugProtocol.NextArguments): void {
		this.dbg.step();
		this.sendResponse(res);
	}

	protected stepBackRequest(res: DebugProtocol.StepBackResponse, _args: DebugProtocol.StepBackArguments): void {
		this.dbg.step(true);
		this.sendResponse(res);
	}

	protected evaluateRequest(res: DebugProtocol.EvaluateResponse, args: DebugProtocol.EvaluateArguments): void {
		let reply: string | undefined = undefined;
		if (args.context === 'repl') {
			// 'evaluate' は 'repl' からのブレークポイントの作成と削除をサポート
			const matches = /new +([0-9]+)/.exec(args.expression);
			if (matches && matches.length === 2) {
				const mbp = this.dbg.setBreakPoint(this.dbg.sourceFile, this.convertClientLineToDebugger(parseInt(matches[1])));
				const bp = <DebugProtocol.Breakpoint> new Breakpoint(mbp.verified, this.convertDebuggerLineToClient(mbp.line), undefined, this.createSource(this.dbg.sourceFile));
				bp.id = mbp.id;
				this.sendEvent(new BreakpointEvent('new', bp));
				reply = 'breakpoint created';
			}
			else {
				const matches = /del +([0-9]+)/.exec(args.expression);
				if (matches && matches.length === 2) {
					const mbp = this.dbg.clearBreakPoint(this.dbg.sourceFile, this.convertClientLineToDebugger(parseInt(matches[1])));
					if (mbp) {
						const bp = <DebugProtocol.Breakpoint> new Breakpoint(false);
						bp.id = mbp.id;
						this.sendEvent(new BreakpointEvent('removed', bp));
						reply = 'breakpoint deleted';
					}
				}
				else {
					const matches = /progress/.exec(args.expression);
					if (matches && matches.length === 1) {
						if (this._reportProgress) {
							reply = 'progress started';
							this.progressSequence();
						} else {
							reply = `frontend doesn't support progress (capability 'supportsProgressReporting' not set)`;
						}
					}
				}
			}
		}

		res.body = {
			result: reply ?reply :`evaluate(context: '${args.context}', '${args.expression}')`,
			variablesReference: 0
		};
		this.sendResponse(res);
	}

	private	isProgressCancellable	= true;
	private async progressSequence() {
		const ID = '' + this._progressId++;

		await timeout(100);

		const title = this.isProgressCancellable ? 'Cancellable operation' : 'Long running operation';
		const startEvent: DebugProtocol.ProgressStartEvent = new ProgressStartEvent(ID, title);
		startEvent.body.cancellable = this.isProgressCancellable;
		this.isProgressCancellable = !this.isProgressCancellable;
		this.sendEvent(startEvent);
		this.sendEvent(new OutputEvent(`start progress: ${ID}\n`));

		let endMessage = 'progress ended';
		for (let i = 0; i < 100; ++i) {
			await timeout(500);
			this.sendEvent(new ProgressUpdateEvent(ID, `progress: ${i}`));
			if (this._cancelledProgressId === ID) {
				endMessage = 'progress cancelled';
				this._cancelledProgressId = undefined;
				this.sendEvent(new OutputEvent(`cancel progress: ${ID}\n`));
				break;
			}
		}
		this.sendEvent(new ProgressEndEvent(ID, endMessage));
		this.sendEvent(new OutputEvent(`end progress: ${ID}\n`));

		this._cancelledProgressId = undefined;
	}

	protected dataBreakpointInfoRequest(res: DebugProtocol.DataBreakpointInfoResponse, args: DebugProtocol.DataBreakpointInfoArguments): void {
		res.body = {
			dataId: null,
			description: 'cannot break on data access',
			accessTypes: undefined,
			canPersist: false
		};

		if (args.variablesReference && args.name) {
			const id = this.hdlsVar.get(args.variablesReference);
			if (id.startsWith('global_')) {
				res.body.dataId = args.name;
				res.body.description = args.name;
				res.body.accessTypes = ['read'];
				res.body.canPersist = true;
			}
		}

		this.sendResponse(res);
	}

	protected setDataBreakpointsRequest(res: DebugProtocol.SetDataBreakpointsResponse, args: DebugProtocol.SetDataBreakpointsArguments): void {
		this.dbg.clearAllDataBreakpoints();

		res.body = {breakpoints: []};
		for (let dbp of args.breakpoints) {
			// assume that id is the "address" to break on
			// id がブレークする "アドレス "であると仮定します。
			const ok = this.dbg.setDataBreakpoint(dbp.dataId);
			res.body.breakpoints.push({verified: ok});
		}

		this.sendResponse(res);
	}

	protected completionsRequest(res: DebugProtocol.CompletionsResponse, _args: DebugProtocol.CompletionsArguments): void {
		res.body = {
			targets: [
				{
					label: 'item 10',
					sortText: '10'
				},
				{
					label: 'item 1',
					sortText: '01'
				},
				{
					label: 'item 2',
					sortText: '02'
				},
				{
					label: 'array[]',
					selectionStart: 6,
					sortText: '03'
				},
				{
					label: 'func(arg)',
					selectionStart: 5,
					selectionLength: 3,
					sortText: '04'
				}
			]
		};
		this.sendResponse(res);
	}

	protected cancelRequest(_res: DebugProtocol.CancelResponse, args: DebugProtocol.CancelArguments) {
		if (args.requestId) this.mapCancelationTokens.set(args.requestId, true);
		if (args.progressId) this._cancelledProgressId= args.progressId;
	}

	//---- helpers

	private createSource(filePath: string): Source {
		return new Source(basename(filePath), this.convertDebuggerPathToClient(filePath), undefined, undefined, 'mock-adapter-data');
	}
}
